import { Box } from "@chakra-ui/react"
// chakras
import { ChakrasAccordion } from "../Chakras/Accordion";
import { ChakraCheckboxGroup } from "../Chakras/CheckBox";
export const Home = () => {
  return <Box p={5} boxSizing='border-box'>
    <Box maxW="800px" mx="auto">
      <ChakrasAccordion />
      <ChakraCheckboxGroup
        defaultCheck={['naruto']}
        checkList={[
          {
            value: "naruto",
            name: "Naruto"
          },
          {
            value: "sasuke",
            name: "Sasuke"
          },
          {
            value: "kakashi",
            name: "Kakashi"
          },
        ]}
        style={{
          checkboxGroup: {},
          stack: { py: 5 },
          checkbox: {}
        }}
      />
    </Box>
  </Box>
}