import { Flex, Square } from '@chakra-ui/react';
import { Link, useLocation } from "react-router-dom";
// chakra comps
import { ChakraTabs } from "../Chakras/Tabs";

export const Header = () => {
  const navList = [
    {
      value: "Home",
      route: "/home",
    },
    {
      value: "AboutUs",
      route: "/about-us",
    },
    {
      value: "Product",
      route: "/product",
    },
    {
      value: "Other",
      route: "/other",
    },
  ]
  const pathname = useLocation().pathname;
  const fetchInitPathname = () => {
    switch (pathname) {
      case navList[1].route:
        return 1;
      case navList[2].route:
        return 2;
      case navList[3].route:
        return 3;
      default:
        return 0;
    }
  }
  return <Flex bg='#8647ff' h='75px' color='white'>
    <Square w='150px' fontSize="3xl" fontWeight="extrabold">LOGO</Square>
    <ChakraTabs
      index={fetchInitPathname()}
      list={navList}
      style={{
        tabs: {
          colorScheme: 'green'
        },
        tablist: {
          h: '100%',
          cursor: 'pointer'
        },
        tab: {
          fontWeight: '600',
          p: '0'
        }
      }}
      children={(v) => <Link style={{ "display": "flex", "alignItems": "center", "width": "100%", "height": "100%", padding: '0 25px' }} to={v.route}>{v.value}
      </Link>}
    />
  </Flex>
}