import React from "react";
import { Tabs, TabList, Tab } from '@chakra-ui/react'

export const ChakraTabs = ({
  index = 0,
  list = [],
  style = {
    tabs: {},
    tablist: {},
    tab: {}
  },
  children
}) => {
  return <Tabs {...style.tabs} defaultIndex={index}>
    <TabList {...style.tablist}>
      {list.map((v, k) => <Tab {...style.tab} key={k}>
        {children(v)}
      </Tab>)}
    </TabList>
  </Tabs>
}