import { Stack, Checkbox, CheckboxGroup } from '@chakra-ui/react';

export const ChakraCheckboxGroup = ({
  defaultCheck = [],
  checkList = [],
  style = {
    checkboxGroup: {},
    stack: {},
    checkbox: {}
  }
}) => {
  return <CheckboxGroup colorScheme='teal' {...style.checkboxGroup} defaultValue={defaultCheck}>
    <Stack spacing={[1, 5]} direction={['column', 'row']} {...style.stack}>
      {checkList.map((v, k) =>
        <Checkbox key={k} {...style.checkbox} value={v.value}>{v.name}</Checkbox>)}


    </Stack>
  </CheckboxGroup>
}
